.cm-background {
  background-image: url('../public/imgs/background.jpg');
  background-attachment: fixed;
  z-index: -9999;
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  cursor: none;
}

/* 当屏幕宽度小于或等于768px时 */
@media (max-width: 768px) {
  .cm-background {
      background-image: url('../public/imgs/bg_mobile.jpg');
  }
}


/*页面加载动画*/
* {
  animation-name: fadeIn;
  animation-duration: 1s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


.test{
  position: absolute;
  z-index: 999999;
  top: 10vh;
}

/* 自定义鼠标样式 */
.custom-cursor {
  position: fixed;
  width: 10px;
  height: 10px;
  background-color: #f596aa;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 9999;  /* 确保在所有其他元素之上 */
  animation-name: ripple;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
}

/* 自定义鼠标背景 */
.custom-cursor-bg {
  position: fixed;
  width: 20px;
  height: 20px;
  background-color: #f7f3ec50;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 9998;
  transition: top 0.05s, left 0.05s;  /* 延迟效果 */
  animation-name: ripple-bg;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
}

.visible {
  display: block;
  animation-name: none; /* 取消动画 */
}

.hidden {
  display: none;
  animation-name: ripple, ripple-bg; /* 应用动画 */
}

/*选中文本背景颜色*/
::selection {
  background: #f596aa90; /* 粉色 */
}
