.pagination {
    margin-top: 10vh;
    margin-bottom: 10vh;
}

.pagination button {
    margin: 1vh;
    height: 3vh;
    width: 3vw;
    border-radius: 20px;
    border: none;
}

.pagination button:hover {
    background-color: #f1f1f150;
    cursor: none;
}

.pagination .active {
    background-color: #4CAF5090;  /* 你可以选择你喜欢的颜色 */
    color: white;
}

/* 当屏幕宽度小于或等于768px时 */
@media (max-width: 768px) {
    .pagination button {
        width: 5em;
        margin: 0.5em;
    }
}