.About_Container {
    position: absolute;
    background-color: #f7f3ec99;
    border-radius: 20px;
    width: 50vw;
    height: 30vh;
    text-align: center;
    top: 30vh;
    left: 25vw;
    font-size: 3em;
}

.qiangdiao {
    color: #f596aa;
}

.About_Container a {
    color: #f596aa;
}

/* 当屏幕宽度小于或等于768px时 */
@media (max-width: 768px) {
    .About_Container {
        width: 80vw;
        height: 40vh;
        top: 20vh;
        left: 10vw;
        font-size: 1em;
    }
}