.Opps_Container {
    position: absolute;
    background-color: #f7f3ec99;
    border-radius: 20px;
    width: 50vw;
    height: 30vh;
    text-align: center;
    top: 30vh;
    left: 25vw;
}

.Opps {
    font-size: 3vw;
}

.Oppsimg {
    height: 10vh;
}

/* 当屏幕宽度小于或等于768px时 */
@media (max-width: 768px) {
    .Opps_Container {
        width: 80vw;
        height: 40vh;
        top: 20vh;
        left: 10vw;
    }

    .Opps {
        font-size: 5vw;
    }

    .Oppsimg {
        height: 20vh;
    }
}