.glow-dot {
    position: fixed;
    background-color: white;
    border-radius: 50%;
    box-shadow: 0 0 5px 5px rgba(255, 255, 255, 0.9);
    opacity: 0;
    animation: glow 4.5s ease-in-out forwards;
    z-index: 2;
    pointer-events: none;
  }
  
  @keyframes glow {
    0% {
      transform: scale(0.1);
      opacity: 0;
    }
    50% {
      transform: scale(1.5);
      opacity: 0.5;
    }
    100% {
      transform: scale(0.1);
      opacity: 0;
    }
  }
  