.arts-page {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    z-index: 2;
}

.arts-page::before{
    position: fixed;
    top: 0;
    background-color: #00000090;
    content: "";
    width: 100vw;
    height: 100vh;
    pointer-events: none;
    z-index: -1;
}
/*瀑布流容器*/
.masonry {
    column-count: 4;  
    margin-top: 100vh;
    width: 70%;
    z-index: 1;
}

/* 瀑布流元素初始状态 */
.masonry-item {
    margin-left: 20%;
    margin-right: 20%;
    max-width: 100%;
    break-inside: avoid;
    transition: all 0.5s ease;  /* 平滑过渡 */
    border-radius: 20px;
  }
  
  /* 悬停状态 */
  .masonry-item:hover {
    transform: scale(1.5);  /* 放大一点 */
    z-index: 9999 !important;  /* 提高 z-index */
  }
  
  /* 蒙版样式 */
  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #00000099;  /* 99% 黑暗 */
    pointer-events: none;
    display: none;
    transition: all 0.5s ease;
}
.dis {
    display: block;
    z-index: 0;
}

/* 当屏幕宽度小于或等于768px时 */
@media (max-width: 768px) {
    .masonry {
        column-count: 2;
    }
    .masonry-item {
        margin-left: 0%;
        margin-right: 0%;
    }
    .masonry-item:hover {
        transform: scale(1.2);  /* 放大一点 */
    }
  }