.navbar {
    display: flex;
    position: fixed;
    color: #f7f3ec;
    font-size: 1.5vh;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 5vh;
    z-index: 9999;
    background-color: #00000060;  /* 纯黑色背景 */
  }
  
  .navbar:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 6vh;  /* 影子的高度 */
    background: linear-gradient(to bottom, #efbb2440, transparent);  /* 从金色渐变到透明 */
    pointer-events: none;  /* 防止遮挡鼠标事件 */
    z-index: 9999;
  }

  .homebt {
    left: 1vw;
    top: 0;
    position: absolute;
    font-size: 3vh;
    transition: color 2s, font-size 1s,text-shadow 2s;
}
  .homebt:hover {
    font-size: 3.2vh;
  }

  .tkbt {
    right: 1vw;
    position: absolute;
    font-size: 1.5vh;
    transition: color 2s, font-size 1s,text-shadow 2s;
}
  .tkbt:hover {
    font-size: 2vh;
  }

  .tkhide {
    display: none;
  }

  a {
    cursor: none;
    text-decoration: none;
  }
  
  .nav_scrolled{
    left: 50%;  /* 水平居中 */
    top: 50%;  /* 垂直居中 */
    transform: translate(-50%, -50%);  /* 确保元素自身也居中 */
    animation: moveToCenter 1s ease;
  }
  
  /* 定义移动到中心的动画 */
  @keyframes moveToCenter {
    0% { left: 1vw; top: 0; transform: translate(0, 0); }
    100% { left: 50%; top: 50%; transform: translate(-50%, -50%); }
  }

  .nav_back{
    animation: moveBack 1s ease;
  }

  /* 定义移动回原位的动画 */
  @keyframes moveBack {
    0% { left: 50%; top: 50%; transform: translate(-50%, -50%); }
    100% { left: 1vw; top: 0; transform: translate(0, 0); }
  }